import React from 'react';
import BlogCards from "./Blogcards";

function Blogsec(){
    let Blogs = [
        {
            id: 1,
            title: "Blog 1",
            imageUrl: "https://s3.envato.com/files/238233065/preview.jpg",
            cardtext: "This Blog is about..."
        },
        {
            id: 2,
            title: "Blog 2",
            imageUrl: "data:image/jpeg;base64,",
            cardtext: "This Blog is about..."
        },
        {
            id: 3,
            title: "Blog 3",
            imageUrl: "https://images.unsplash.com/photo-1544006659-f0b21884ce1d?ixid=MnwxMjA3fDB8MHxzZWFyY2h8M3x8bWFuJTIwd29ya2luZyUyMG9uJTIwY29tcHV0ZXJ8ZW58MHx8MHx8&ixlib=rb-1.2.1&w=1000&q=80",
            cardtext: "This Blog is about..."
        },
    ];
    return(
        <div className="container my-3 pt-5 pb-3 text-center">
            <h1 className="font-weight-light text-dark display-6">My <span className="text-info">Blogs</span></h1>
            <p className="lead">I share my views on science and technology in these blogs.</p>
            <div className="row my-4 py-4">
            {
                    Blogs.map((blog) => (
                        <div key={blog.id} className="col-12 col-md-4 py-3">
                            <BlogCards
                                blog = {blog}
                            />
                        </div>
                     ) )
                }
            </div>
            <div className="pe-2 text-end">
                <a href="/" className="text-dark" style={{textDecoration: "none"}}>
                    <h4 className="fw-normal">More Blogs
                        <i className="ms-1 fas fa-arrow-right" style={{fontSize: "80%"}}></i></h4>
                </a>
            </div>
        </div>
    );
}

export default Blogsec;