import React from 'react';
import Navbar from './navbar';
import Footersec from './footer';
import {BrouserRouter, BrowserRouter, Route, Switch} from 'react-router-dom';
import Contact from './Contact';
import Reccomendation from './Giverec';
import Homepage from './Homepage';
import Newblog from './Blogwrite';


function App() {
  return (
    <BrowserRouter>
      <Navbar/>
      <Switch>
        <Route path="/PORTFOLIO_WEBSITE/" exact component={Homepage}></Route>
        <Route path="PORTFOLIO_WEBSITE/contactpage" component={Contact}></Route>
        <Route path="PORTFOLIO_WEBSITE/write-a-reccomendation" component={Reccomendation}></Route>
        <Route path="PORTFOLIO_WEBSITE/addnewblog" exact component={Newblog}></Route>
      </Switch>
      <Footersec />
    </BrowserRouter>
  );
} 

export default App;
