import { event } from 'jquery';
import React, {Component} from 'react';

class Reccomendation extends React.Component {
    state={
        name: "",
        email: "",
        company: "",
        designation: "",
        reccomment: "",
        isShow: false,
    };

    onChange = (event) => {
        this.setState({
            [event.target.name]: event.target.value,
        });
    };

    onSubmit = (event) => {
        event.preventDefault();
        let thankyou = document.getElementById('thanks');
        thankyou.style.visibility = 'visible';
    };

    render(){
        let {name} = this.state;
    return (
        <div className="container text-center my-5 py-3">
            <div>
                <h1 className="fw-light mb-5"><span className="text-info">Thank You!</span> for your time.</h1>
            </div>
            <div className="row justify-content-center">
                <div className="col-11 col-lg-6">
                    <form>
                        <div className="form-group my-3">
                            <label htmlFor="name" className="float-start ps-1">Name *</label>
                            <input type="text" name="name" className="form-control" onChange={this.onChange}></input>
                        </div>
                        <div className="form-group my-3">
                            <label htmlFor="email" className="float-start ps-1">Email *</label>
                            <input type="email" name="email" className="form-control" onChange={this.onChange}></input>
                        </div>
                        <div className="form-group my-3">
                            <label htmlFor="company" className="float-start ps-1">Company / Institution *</label>
                            <input type="text" name="company" className="form-control" onChange={this.onChange}></input>
                        </div>
                        <div className="form-group my-3">
                            <label htmlFor="designation" className="float-start ps-1">Designation *</label>
                            <input type="text" name="designation" className="form-control" onChange={this.onChange}></input>
                        </div>
                        <div className="form-group my-3">
                            <label htmlFor="reccomment" className="float-start ps-1">Reccomendation *</label>
                            <textarea name="reccomment" rows="5" className="form-control" style={{resize: "none"}} onChange={this.onChange}></textarea>
                        </div>
                        <button type="submit" className="btn btn-dark float-end d-block" onClick={this.onSubmit}>Let's Talk Business!</button>
                    </form>
                </div>
                <h3 id="thanks" className="lead my-3 p-3" style={{visibility: 'hidden'}}>Hey {name}! Thank you so much for your precious Reccomendation. It means a lot!</h3>
            </div>
        </div>
    )
}}

export default Reccomendation;